@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;800&display=swap);
.container_portait img {
  width: 10rem;
}

.container_arrow img{
  width: 3rem;
}

.nav_bar {
  box-shadow: 0px -4px 30px rgba(0, 0, 0, 0.25);
  margin-bottom: 5rem;
}

.navbar-brand {
  color: #7510f7 !important;
}
.btn_hire_me {
  border: 0.15rem solid #7510f7;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  border-radius: 3rem;
  padding: 0.3rem 1rem !important;
  font-size: 1rem;
  font-size: 1.3rem;
}

.btn_hire_me:hover {
  background: #7510f7;
  color: #fff !important;
}

.nav-item {
  font-weight: 500;
  font-size: 1.3rem;
  color: #c32121 !important;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}


